html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: Inter, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
}

:root {
  --white: rgb(255, 255, 255);
  --black: hsl(0, 0%, 12%);
  --brown: hsl(29, 30%, 61%);
  --light-brown: hsl(30, 63%, 70%);
  --sage: hsl(138, 21%, 88%);
  --light-yellow: hsl(40, 43%, 96%);
  --background-color: var(--white);
  --text-color: var(--black);
  --buttons: var(--white);
}

.button {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 37px;
  background-color: var(--buttons);
  font-weight: 800;
  font-size: 1.25rem;
  padding: 14px 20px 14px 20px;
}

body {
  background-image: url(/public/assets/background.png);
  background-repeat: no-repeat;
  background-position: top;
  background-position-y: 60px;
}

header {
  display: flex;
  align-items: center;
  height: 73px;
  background-color: var(--white);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  position: sticky;
  top: 0;
}

svg {
  fill: var(--black);
  transition: 0.4s;
}
svg:hover {
  fill: var(--light-brown);
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 60px;
}
.hero img {
  width: 272px;
}

main {
  display: flex;
  flex-direction: column;
  margin: auto;
  gap: 133px;
  max-width: 302px;
}